@media (min-width: 768px) and (max-width: 1024px) {
  .App {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .slide {
    height: 100%;
  }
}

@media (min-width: 350px) and (max-width: 768px) {
  .App {
    display: grid;
  }
  .slide {
    height:auto;
  }
}



.slide {
  justify-content: center;
  transition-property: all;
  transition-timing-function:ease-in-out;
  transition-duration: 0.3s;
  
}

.slide {
  text-align: center;
  color: rgb(0, 0, 0);
  background-color: aliceblue;
  padding: 60px;
  border-radius: 10px;
  font-size: medium;
  
}
.line {
  margin: -10px;
  font-size: large;
}

hr {
  margin: 1.5rem 3px;
}

.money {
  font-size: 40px;
  padding-right: 15px;
}

span {
  font-size: 15px;
}

.storage,
.projects,
.access,
.case1,
.case2,
.case3,
.case4 {
  text-align: left;
}

